import React, { ChangeEvent, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../../../renderer/store/hooks';
import { LOGGING_IN } from '../../../renderer/_reducers/_slices/loginSlice';
import { API_URL } from '../../../renderer/CONSTANT_URL';

export { PasswordStage };

function PasswordStage({ setMove }: { setMove: React.Dispatch<React.SetStateAction<number>> }) {
    const dispatch = useAppDispatch();

    const id = useAppSelector((state) => state.loginSlice.currentId);
    const [password, setPassword] = useState<string>('');
    const [isChecked, setIsChecked] = useState<boolean>(false);

    const handlePrev = () => {
        setMove((prev) => prev + 100);
    };

    const handlePasswordChange = (event: ChangeEvent<HTMLInputElement>) => {
        setPassword(event.target.value);
    };
    const handleCheckChange = (event: ChangeEvent<HTMLInputElement>) => {
        setIsChecked(event.target.checked);
    };

    async function handleClick() {
        // ÌÜ±Î∞îÏùò Îí§Î°úÍ∞ÄÍ∏∞ Î≥¥Ïó¨Ï§òÎèÑ Îê®
        dispatch(LOGGING_IN(false));

        // Î°úÍ∑∏Ïù∏ ÌÜµÏã†
        try {
            const body = new URLSearchParams();
            body.append('userId', id);
            body.append('password', password);
            body.append('keepLogin', isChecked ? 'true' : 'false');

            const res = await fetch(`${API_URL}/users/login`, {
                credentials: 'include',
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body: body.toString(),
            });

            const data = await res.json();

            if (data.success) {
                window.alert('üéâüéâÎ°úÍ∑∏Ïù∏Ïóê ÏÑ±Í≥µÌñàÏäµÎãàÎã§üéâüéâ');
                window.location.href = '/search';
            } else {
                window.alert('Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§');
            }
        } catch (err) {
            console.error(err);
        }
    }

    return (
        <section className="login-password-stage">
            <h3 className="sr-only">ÎπÑÎ∞ÄÎ≤àÌò∏ ÏûÖÎ†•</h3>

            <label htmlFor="loginPassword">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
            <input type="password" id="loginPassword" onChange={handlePasswordChange} />

            <label htmlFor="loginPersist">Î°úÍ∑∏Ïù∏ Ïú†ÏßÄ</label>
            <input type="checkbox" id="loginPersist" onChange={handleCheckChange} />

            <div className="button-group-move">
                <button type="button" onClick={handlePrev} aria-label="Ïù¥Ï†Ñ ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    ÏïÑÏù¥Îîî
                </button>
                <button type="button" onClick={handleClick} aria-label="Îã§Ïùå ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    Î°úÍ∑∏Ïù∏
                </button>
            </div>
        </section>
    );
}
